@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .footer {
        &_mobile {
            display: flex;
            a {
                margin: 0 3px;
                img {
                    width: 100%; } } }

        &_wrapper > div {
            width: 50%;
            &:nth-child(1) {
                width: 25%; }
            &:nth-child(3) {
                width: 25%; } } }
				display: flex;
				justify-content: flex-end;


    @media (max-width: 991px) {
        .menu {
            &_item {
                padding-right: 8px; }
            &_link {
                font-size: 10px; } }
        .subheader {
            &_official {
                font-size: 11px; }
            &_phone {
                font-size: 18px; }
            &_btn {
                margin-top: 21px;
                height: 40px;
                width: 100%; } }
        .promo {
            &_header {
                font-size: 40px; }
            &_subheader {
                font-size: 30px; } }
        .advanteges {
            &_card {
                flex-direction: column;
                align-items: center; }
            &_icon {
                margin-right: 0px; }
            &_title {
                margin-top: 20px; }
            &_discr {
                width: 300px;
                margin-top: 20px; } }
        .mobile {
            &_item {
                width: 290px;
                &__subtitle {
                    font-size: 16px; } } }
        .footer {
            &_wrapper {
                flex-wrap: wrap; }
            &_wrapper > div {
                width: 100%;
                display: flex;
                justify-content: center;
                margin-bottom: 20px;
                &:nth-child(1) {
                    width: 100%; }
                &:nth-child(3) {
                    width: 100%;
                    justify-content: center; } }
            &_icons {
                display: flex;
                a {
                    margin-top: 5px; } } } } }

@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0 ,0 ,.8 );
        transition: 0.6s all;
        &_item {
            margin: 10px 0;
            border: none;
            padding-right: 0; }
        &_link {
            font-size: 20px; }
        &_active {
            left: 0; } }
    .gamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #ffffff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            color: #ffffff;
            font-size: 16px;
            font-weight: 400px;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            width: 180px;
            height: 30px;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
            &_header {
                line-height: 45px; } } }
@media (max-width: 575px) {
    nav {
        height: 30px; }
    header {
        height: 30px; }
    .subheader {
        height: 60px;
        top: 30px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 16px;
            img {
                width: 100%; } }
        &_phone {
            font-size: 12px;
            font-weight: 400px;
            margin-top: 5px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 10px;
            margin-top: 3px; } }
    .promo {
        min-height: 400px;
        padding: 73px 0 35px 0;
        &_description {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px;
            font-size: 16px;
            &:hover {
                background-image: linear-gradient(to top, $color 0%, #4ce2ff 100%); } } }

    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; } }
    .advantages {
        padding: 22px 0 36px 0;
        &_card {
            width: 100%; }
        &_icon {
            width: 100px;
            height: 100px; }
        .title {
            font-size: 16px;
            margin-top: 15px;
            line-height: 16px; }
        &_text {
            width: 100%;
            margin-top: 27px; } }
    .mobile {
        padding: 20px 0 40px 0;
        &_item {
            height: 254px;
            padding: 81px 40px 0 40px;
            &_subtitle {
                font-size: 18px;
                line-height: 31px; }
            &_plus {
                bottom: 28px;
                &:hover {}
                &:before, &:after {
                    background-color: $color; } } }
        .title {
            font-size: 16px;
            margin-top: 15px;
            line-height: 16px; }
        .row {
            margin-top: 30px; } }
    .choice {
        padding: 25px 0 33px 0;
        min-height: 390px;
        background: #000; }
    .require {
        padding-top:  45px;
        .row {
            margin-top: 0px;
            &_block {
                margin: 30px 0 0 0; } } }
    .cities {
        min-height: 360px;
        padding: 25px 0 30px 0;
        &_link {
            width: 200px;
            display: block;
            bottom: 30px;
            line-height: 15px;
            font-size: 14px; }
        .title {
            font-size: 16px;
            margin-top: 15px;
            line-height: 16px; }
        .subtitle {
            font-size: 14px;
            line-height: 21px;
            margin-top: 11px; } }
    footer {
        padding: 25px 0 20px 0; }
    .footer {
        &_divider {
            width: 253px;
            margin: 0 auto; }
        &_wrapper {
            padding-top: 22px;
            flex-wrap: wrap; }
        &_icons {
            flex-direction: row; }
        &_mobile {
            flex-direction: row; }
        &_wrapper > div {
            margin-bottom: 20px;
            &:nth-child(1) {
                order: 2; }
            &:nth-child(2) {
                order: 1; }
            &:nth-child(3) {
                order: 3; } }
        &_main {
            width: 390px;
            &_nav {
                padding: 0;
                font-size: 14px; }
            &_nav_2 {
                flex-wrap: wrap;
                justify-content: center;
                a {
                    margin: 0 7px; } }
            &_language {
                margin-top: 10px; } } } }












